body {
    font-family: "Roboto", sans-serif;
    font-size: 2vw;
    font-weight: bold;
    margin: auto;
}

.container .item {
    flex: auto;
    width: 20%;

    height: 20vw;


    background-color: rgb(255, 100, 100);

    $border-width: 0.5vw;
    border-radius: calc(2 * $border-width);
    border-width: $border-width;
    border-style: solid;
    border-color: rgb(220, 20, 20);

    text-align: center;
    color: rgb(255, 255, 240);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container {
    margin: 2vw;
    display: flex;
    flex-wrap: wrap;
    gap: 1vw;
}